.Treeview {
    width: 100%;
    height: 100vh;
    display: flex;
    .Input {
        width: 50%;
        background: #fff;
        padding: 30px;
        border: 5px solid #ddd;
        .Error {
            color: red;
            font-weight: bold;
            margin-top: 10px;
        }
    }

    .Tree {
        width: 50%;
        background: #fff;
        border: 5px solid #ddd;
        padding-left: 10px;
    }
}
