.InputTextArea {
    background: blue;
    height: 85%;

    textarea {
        width: 100%;
        height: 100%;
        resize: none;
        margin: 0;
        border: 1px solid #ddd;
        outline: none;
        padding: 20px;
        font-size: 18px;
    }
    button {
        margin-top: 30px;
        background: #fff;
        border: 1px solid #ccc;
        padding: 10px 20px;
        cursor: pointer;
        outline: none;
        border-radius: 10px;
    }
    button:hover {
        box-shadow: 0 0 5px 1px rgba(0,0,0,0.2);
    }
    button:active {
        box-shadow: 0 0 10px 1px rgba(0,0,255,0.4);
    }
}